.c-primary-navlist {
  list-style-type: none;
  --gap: 50px;
  position: fixed;
  top: 63px;
  bottom: 0;
  flex-direction: column;
  z-index: 9999;
  height: 100%;
  left: 0;
  right: 0;
  background-color: hsl($clr-very-dark-violet);
  align-items: center;
  padding: 0;
  background-color: hsl($clr-very-dark-violet);
  background-image: url('../../images/backgrounds/bg-pattern-mobile-nav.svg');
  background-position: right 83%;
  background-repeat: no-repeat;
  padding: 50px;
  background-size: contain;


  >.item {
    // margin-left: 28px;
    line-height: 10px;

    &:last-child {
      // margin-left: 7px;
      width: 100%;
    }
  }

  &.hidden {
    left: 100%;
  }

  @media screen and (min-width: 375px) {
    position: unset;
    background-image: unset;
    background-color: unset;
    flex-direction: row;
    padding: 0;
    --gap: 27px;

    >.item {
      &:last-child {
        width: unset;
      }
    }

  }

}